.content {

  &_pt {
    padding-top: 100px;
  }

  &_pb {
    padding-bottom: 100px;
  }

  &_pr {
    padding-right: 160px;
  }

  &_pl {
    padding-left: 160px;
  }

  & > h1 {
    margin: 20px 0 0 0;
    font-family: $font-family-one;
    font-weight: 300;
    font-size: 75px;
    color: rgba(0, 0, 0, .8);
  }

  & > h2 {
    margin: 20px 0 0 0;
    font-family: $font-family-one;
    font-weight: 300;
    font-size: 60px;
    color: rgba(0, 0, 0, .8);
  }

  & > h3 {
    margin: 45px 0 0 0;
    font-family: $font-family-one;
    font-weight: 300;
    font-size: 30px;
    color: #000;
  }

  & > p {
    margin: 80px 0 0 0;
    font-family: $font-family-one;
    font-weight: 300;
    font-size: 25px;
    color: #616161;
    line-height: 1.7;

    & a {
      color: inherit;
      text-decoration: none;
      padding-bottom: 8px;
      //border-bottom: 1px solid #95989A;
      transition: all .2s ease-in-out;

      &:hover {
        color: #000;
        //border-bottom: 1px solid #000;
      }

    }

  }

  & > img {
    width: 100%;
  }

  &_contact {

    & > h1 {
      margin: 20px 0 0 0;
      font-size: 70px;
    }

    & > p {
      margin: 50px 0 0 0;
      font-size: 35px;
    }

  }

  &__btn-wrap {

    &_tar {
      text-align: right;
    }

  }

}


/* Medium Devices, Desktops */
@media only screen and (max-width : 1024px) {

}

/* Medium Devices, Desktops */
@media only screen and (max-width : 992px) {

  .content {

    & > h1 {
      margin: 20px 0 0 0;
      font-size: 70px;
    }

    & > h2 {
      margin: 0;
      font-size: 60px;
    }

    & > h3 {
      margin: 45px 0 0 0;
      font-size: 27px;
    }

    & > p {
      margin: 60px 0 0 0;
      font-size: 28px;
    }

    &_contact {

      & > h1 {
        margin: 10px 0 0 0;
        font-size: 70px;
      }

      & > p {
        margin: 30px 0 0 0;
        font-size: 28px;
      }

    }

    &_card {
      background: #fff;
      padding: 70px!important;
    }

  }

}

/* Small Devices, Tablets */
@media only screen and (max-width : 760px) {

  .content {

    & > h1 {
      margin: 0;
      font-size: 35px;
    }

    & > h2 {
      margin: 0;
      font-size: 25px;
    }

    & > h3 {
      margin: 45px 0 0 0;
      font-size: 13px;
    }

    & > p {
      margin: 30px 0 0 0;
      font-size: 14px;
    }

    &_pt {
      padding-top: 70px;
    }

    &_contact {

      & > h1 {
        margin: 0;
        font-size: 35px;
      }

      & > p {
        margin: 30px 0 0 0;
        font-size: 14px;
      }

    }

    &_card {
      padding: 30px!important;
    }

    &_pb {
      padding-bottom: 90px;
    }

  }

}

