.section {
  position: relative;
  z-index: 1;
  height: 100%;
  padding: 90px 0;
  background: #fff;
}

.section-bg {
  //height: 100vh;
  //min-height: 1280px;
  position: relative;
  z-index: 1;

  padding: 100px 0;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;

  &_ptbn {
    padding: 0;
  }

  &_1 {
    background-image: url("../images/bg/1.jpg");
  }

  &_2 {

  }

  &_3 {
    background-image: url("../images/bg/3.jpg");
  }

  &_4 {

  }

  &_5 {
    background-image: url("../images/bg/5.jpg");
  }

  &_6 {

  }

  &_7 {
    background-image: url("../images/bg/7.jpg");
  }

  &_8 {
    background-image: url("../images/bg/8.jpg");
  }

  &_9 {

  }

  &_10 {
    background-image: url("../images/bg/10.jpg");
  }

  &__mask {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background: rgba(0,0,0,.5);
  }

}

.section-head {
  display: none;
}

/* Medium Devices, Desktops */
@media only screen and (max-width : 992px) {

  .section {

    &_bgn {
      background: transparent;
    }

    &_pn {
      padding: 0!important;
    }

  }

  .section-bg {
    padding: 80px 0;

    &_1 {
      background-image: url("../images/bg/1_t.jpg");
    }

    &_2 {
      background-image: url("../images/bg/2_t.jpg");
    }

    &_3 {
      background-image: url("../images/bg/3_t.jpg");
    }

    &_4 {
      background-image: url("../images/bg/4_t.jpg");
    }

    &_5 {
      background-image: url("../images/bg/5_t.jpg");
    }

    &_6 {
      background-image: url("../images/bg/6_t.jpg");
    }

    &_7 {
      background-image: url("../images/bg/7_t.jpg");
    }

    &_8 {
      background-image: url("../images/bg/8_t.jpg");
    }

    &_9 {
      background-image: url("../images/bg/9_t.jpg");
    }

    &_10 {
      background-image: url("../images/bg/10_t.jpg");
    }

  }

  .section-head {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 350px 35px;

    &__title {
      margin: 0;
      font-family: $font-family-one;
      font-weight: 300;
      font-size: 60px;
      color: #fff;
      text-align: center;
    }

    &__img {
      margin-top: 60px;
      width: 114px;
      height: 85px;
    }

  }

}

/* Small Devices, Tablets */
@media only screen and (max-width : 760px) {

  .section-bg {
    padding: 35px 0 180px 0;

    &_ptbn {
      padding: 35px 0 180px 0;
    }

    &_1 {
      background-image: url("../images/bg/1_m.jpg");
    }

    &_2 {
      background-image: url("../images/bg/2_m.jpg");
    }

    &_3 {
      background-image: url("../images/bg/3_m.jpg");
    }

    &_4 {
      background-image: url("../images/bg/4_m.jpg");
    }

    &_5 {
      background-image: url("../images/bg/5_m.jpg");
    }

    &_6 {
      background-image: url("../images/bg/6_m.jpg");
    }

    &_7 {
      background-image: url("../images/bg/7_m.jpg");
    }

    &_8 {
      background-image: url("../images/bg/8_m.jpg");
    }

    &_9 {
      background-image: url("../images/bg/9_m.jpg");
    }

    &_10 {
      background-image: url("../images/bg/10_m.jpg");
    }

  }

  .section-head {
    padding: 250px 0;

    &__title {
      font-size: 30px;
    }

    &__img {
      margin-top: 25px;
      width: 64px;
      height: 47px;
    }

  }


}

