.banner {
  position: relative;
  z-index: 1;
  margin-top: 196px;

  &__title {
    margin: 0;
    font-family: $font-family-one;
    font-weight: 300;
    font-size: 60px;
    color: #000;
    line-height: 1.42;
  }

}

.banner-img-box-nf {
  position: relative;

  &__img {
    content: '';
    position: absolute;
    right: 0;
    bottom: 0;

    width: 783px;
    height: 881px;
    background: url("../images/banner/1.png");
    background-repeat: no-repeat;
    background-size: 783px 881px;
  }

  &__img-2 {
    content: '';
    position: absolute;
    right: 0;
    bottom: 170px;

    width: 828px;
    height: 736px;
    background: url("../images/banner/4.png");
    background-repeat: no-repeat;
    background-size: 828px 736px;
  }

}

.banner-img-box {
  position: fixed;
  right: 0;
  top: 0;
  bottom: 0;
  width: 100%;
  min-height: 1090px;

  &__img {
    content: '';
    position: absolute;
    right: 0;
    bottom: 0;

    width: 783px;
    height: 881px;
    background: url("../images/banner/1.png");
    background-repeat: no-repeat;
    background-size: 783px 881px;
  }

  //@media
  //(-webkit-min-device-pixel-ratio: 1.5),
  //(   min--moz-device-pixel-ratio: 1.5),
  //(     -o-min-device-pixel-ratio: 3/2),
  //(        min-device-pixel-ratio: 1.5),
  //(                min-resolution: 1.5dppx) {
  //  .banner-img  {
  //    background: url("../images/banner/1@2x.png");
  //  }
  //}

}

@media only screen and (max-width : 1600px) {

  .banner-img-box-nf {

    &__img-2 {
      right: -120px;
    }

  }

}

@media only screen and (max-width : 1400px) {

  .banner-img-box-nf {

    &__img-2 {
      right: -200px;
    }

  }

}

@media only screen and (max-width : 1300px) {

  .banner {
    position: fixed;
    right: 0;
    top: 0;
    bottom: 0;
    width: 100%;

    display: flex;
    align-items: center;
    height: 100%;
    margin-top: auto;

  }

  .banner-img-box-nf {
    display: none;
  }

  .banner-img-box {
    min-height: auto;

    &__img {
      top: 180px;
      //bottom: 0;
      right: -185px;
      margin: auto 0;

      width: 384px;
      height: 773px;
      background: url("../images/banner/2.png");
      background-size: 384px 773px;
    }

    &__img-2 {
      right: -100px;
    }

  }

}

/* Medium Devices, Desktops */
@media only screen and (max-width : 1024px) {

}

/* Medium Devices, Desktops */
@media only screen and (max-width : 992px) {

  .banner {
    position: relative;
    right: 0;
    top: 275px;
    bottom: initial;
    width: 100%;

    display: flex;
    align-items: center;
    height: 100%;
    margin-top: auto;

    &__title {
      font-size: 50px;
    }

  }

}

/* Small Devices, Tablets */
@media only screen and (max-width : 760px) {

  .banner {
    top: 140px;
    margin-top: auto;

    &__title {
      font-size: 21px;
    }

  }

  .banner-img-box {
    min-height: auto;

    &__img {
      top: 100px;
      bottom: initial;
      right: -130px;
      margin: auto 0;

      width: 212px;
      height: 429px;
      background: url("../images/banner/3.png");
      background-size: 212px 429px;
    }

  }

}

