.social {
  position: fixed;
  left: 40px;
  bottom: 40px;
  display: flex;
  flex-direction: column;
  width: 30px;

  &__link {
    display: inline-block;
    margin-bottom: 15px;

    &:last-child {
      margin-bottom: 0;
    }

  }

  &__img {
    max-width: 30px;
    max-height: 30px;
  }

}

/* Medium Devices, Desktops */
@media only screen and (max-width : 992px) {

}

/* Small Devices, Tablets */
@media only screen and (max-width : 760px) {

  .social {
    left: 20px;
    bottom: 20px;
    width: 17px;

    &__link {
      margin-bottom: 10px;
    }

    &__img {
      max-width: 17px;
      max-height: 17px;
    }

  }

}

