.footer {
  margin-top: 100px;

  &__text {
    font-family: $font-family-one;
    font-weight: 300;
    font-size: 20px;
    color: #B7B7B7;
    text-align: center;
  }

}

.footer-text-in-section {
  margin: 20px 0;
  position: absolute;
  z-index: 1;
  bottom: 0;
  width: 100%;
  font-family: $font-family-one;
  font-weight: 300;
  font-size: 20px;
  text-align: center;
  color: #fff;
}

/* Medium Devices, Desktops */
@media only screen and (max-width: 1024px) {

}

/* Medium Devices, Desktops */
@media only screen and (max-width: 992px) {

  .footer-text-in-section {
    color: #B7B7B7;
  }

}

/* Small Devices, Tablets */
@media only screen and (max-width: 760px) {

  .footer {
    margin-top: 20px;

    &__text {
      font-size: 8px;
    }

  }

  .footer-text-in-section {
    margin: 8px 0;
    font-size: 8px;
    color: #B7B7B7;
  }

}

/* Extra Small Devices, Phones */
@media only screen and (max-width: 480px) {

