$font-family-one: "Gilroy Light", sans-serif;

* {
  box-sizing : border-box;
}

html {
  height: 100%;
}

body {
  display: -webkit-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;

  -webkit-box-orient: vertical;
  -webkit-flex-direction: column;
  -ms-flex-direction: column;
  flex-direction: column;

  //min-height: 100vh;
  height: 100%; // fix mobile sticky footer

  font-family: $font-family-one;

  &.toggled {
    overflow: hidden!important;
  }

}

@import "banner";
@import "btn";
@import "container";
@import "content";
