.scroll {
  position: fixed;
  z-index: 1;
  left: 0;
  right: 0;
  bottom: 40px;
  margin-right: auto;
  margin-left: auto;
  width: 163px;
  text-align: center;

  &__img {
    width: 163px;
    vertical-align: top;
  }


  &__text {
    padding-top: 12px;
    font-family: $font-family-one;
    font-weight: 300;
    font-size: 25px;
    color: #000;
    letter-spacing: 0.5;
  }

}

/* Medium Devices, Desktops */
@media only screen and (max-width : 992px) {

  .scroll {
    display: none;
  }

}

