.container {
  position: relative;
  max-width: 1640px;
  width: 100%;
  margin: 0 auto;
  padding: 0 80px;

  &_mh {
    min-height: 820px;
  }

  &_mh-2 {
    min-height: 925px;
  }

}

/* Medium Devices, Desktops */
@media only screen and (max-width : 1024px) {

  .container {
    padding: 0 80px;

    &_mh {
      min-height: auto;
    }

    &_mh-2 {
      min-height: auto;
    }

  }


}

/* Medium Devices, Desktops */
@media only screen and (max-width : 992px) {

  .container {
    padding: 0 80px;
  }

}

/* Small Devices, Tablets */
@media only screen and (max-width : 760px) {

  .container {
    padding: 0 35px;
  }

}

