.team {
  padding: 100px 0;

  &__title {
    margin: 0;
    font-family: $font-family-one;
    font-weight: 300;
    font-size: 40px;
    color: rgba(0,0,0,.8);
    text-transform: uppercase;
  }

}

.team-list {
  margin-left: -5px;

  &__item {
    display: flex;
    margin-top: 70px;
  }

  &__img-box {
    width: 426px;
  }

  &__img {
    width: 100%;
    vertical-align: top;
  }

  &__detail {
    padding-left: 94px;
  }

  &__title {
    margin: 0;
    font-family: $font-family-one;
    font-weight: 300;
    font-size: 35px;
    color: #000;
  }

  &__position {
    margin: 15px 0 0 0;
    font-family: $font-family-one;
    font-weight: 300;
    font-size: 22px;
    color: #616161;
    text-transform: uppercase;
  }

}


/* Medium Devices, Desktops */
@media only screen and (max-width : 992px) {

  .team {
    margin-top: 80px;
    padding: 80px 0;

    &__title {
      font-size: 40px;
    }

  }

  .team-list {
    margin-left: -5px;

    &__item {
      display: flex;
      margin-top: 70px;
    }

    &__img-box {
      width: 170px;
    }

    &__img {
      width: 100%;
      vertical-align: top;
    }

    &__detail {
      padding-left: 40px;
    }

    &__title {
      font-size: 28px;
    }

    &__position {
      margin: 15px 0 0 0;
      font-size: 15px;
    }

  }

}

/* Small Devices, Tablets */
@media only screen and (max-width : 760px) {

  .team {
    margin-top: 35px;
    padding: 35px 0;

    &__title {
      font-size: 23px;
    }

  }

  .team-list {
    margin-left: -5px;

    &__item {
      margin-top: 35px;
    }

    &__img-box {
      width: 90px;
    }

    &__img {
      width: 100%;
    }

    &__detail {
      padding-left: 20px;
    }

    &__title {
      font-size: 14px;
    }

    &__position {
      margin: 5px 0 0 0;
      font-size: 9px;
    }

  }

}

