.header {
  position: relative;
  z-index: 2;
}

.header-box {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.header-nav-box {
  margin-top: 20px;
}

.header-nav {
  display: flex;
  margin: 0;
  padding: 0;
  list-style: none;

  &__item {
    margin-right: 60px;

    &:last-child {
      margin-right: 0;
    }

  }

  &__link {
    font-family: $font-family-one;
    font-weight: 300;
    font-size: 25px;
    color: #000;
    text-decoration: none;
    padding-bottom: 8px;
    border-bottom: 1px solid transparent;

    &_active {
      border-bottom-color: #95989A;
    }

  }

}

.header-menu-btn {
  display: none;
  position: relative;
  padding: 0;
  width: 30px;
  background: transparent;
  border: 0;
  outline: none;
  transition: background ease-in-out .2s;

  &:hover {
    opacity: .8;
  }

  &:before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 2px;
    background: #000;
  }

  &:after {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 2px;
    background: #000;
  }

  & span {
    display: block;
    margin: 9px 0;
    width: 30px;
    height: 2px;
    background: #000;
  }

  &.toggled {

    &:before {
      background: #fff;
    }

    &:after {
      background: #fff;
    }

    & span {
      background: #fff;
    }

  }

}

.header-nav-mask {
  background-color: rgba(0,0,0,.66);
  bottom: 0;
  left: 0;
  opacity: 0;
  position: fixed;
  right: 0;
  top: 0;
  transform: translateZ(0);
  transition: opacity .2s linear;
  visibility: hidden;
  z-index: 0;

  &.toggled {
    opacity: 1;
    visibility: visible;
    z-index: 1;
  }

}

/* Medium Devices, Desktops */
@media only screen and (max-width: 1024px) {

}

/* Medium Devices, Desktops */
@media only screen and (max-width: 992px) {

  .header-nav-box {
    //display: none;
    position: fixed;
    z-index: 99;
    top: 0;
    bottom: 0;
    left: 0;
    margin-top: 0;
    width: 350px;
    height: 100%;
    background: rgba(0, 0, 0, .85);
    overflow-y: auto;
    transform: translateX(-100%);
    transition: transform .15s cubic-bezier(.4, 0, .2, 1);

    &.toggled {
      transform: translateX(0);
      transition-duration: 235ms;
    }

  }

  .header-nav {
    position: relative;
    z-index: 1;
    flex-direction: column;
    padding: 140px 0 0 80px;

    &__item {
      margin-right: 0;
      margin-bottom: 30px;

      &:last-child {
        margin-right: 0;
        margin-bottom: 0;
      }

    }

    &__link {
      font-size: 28px;
      color: rgba(255,255,255,0.3);
      border-bottom: none;

      &_active {
        color: #fff;
      }

    }

  }

  .header-menu-btn {
    display: inline-block;
    position: absolute;
    left: 40px;
    top: 35px;
    z-index: 100;
  }

}

/* Small Devices, Tablets */
@media only screen and (max-width: 760px) {

  .header-nav-box {
    width: 190px;
  }

  .header-nav {
    padding: 100px 0 0 35px;

    &__item {
      margin-right: 0;
      margin-bottom: 30px;

      &:last-child {
        margin-right: 0;
        margin-bottom: 0;
      }

    }

    &__link {
      font-size: 20px;
    }

  }

  .header-menu-btn {
    left: 20px;
    top: 30px;
    width: 16px;

    & span {
      width: 16px;
      margin: 5px 0;
    }

  }

}

